.shop {
    padding-top: 2.5rem;

    .header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 2.5rem;

        h1 {
            font-size: 2rem;
            font-weight: bold;
        }

        input {
            width: 250px;
            padding: 0.93rem 1.12rem 0.93rem 2.87rem;
            border: 1px solid #e3e3e3;
            border-radius: 10px;
            background: url("../../../../../public/img/search.svg") no-repeat 1.12rem 50%;
        }

        ::placeholder {
            color: #e3e3e3;
        }

        input:focus {
            border-color: #e3e3e3;
            outline: none;
        }
    }
}