.header {
    padding-top: 48px;
    padding-bottom: 44px;
    border-bottom: 1px solid #eaeaea;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 972px;
        margin: 0 auto;

        .logo {
            display: flex;
        }

        img {
            margin-right: 1rem;
        }

        .text {
            font-size: 0.87rem;
            color: #9d9d9d;
            letter-spacing: 0.35px;

            h2 {
                font-size: 1.25rem;
                font-weight: bold;
                text-transform: uppercase;
                color: #000000;
                letter-spacing: 0;
            }
        }

        .option {
            display: flex;
            align-items: center;

            .priceBlock {
                display: flex;
                align-items: center;
                margin-right: 1.87rem;
                transition: transform 0.2s ease-in-out;


                .price {
                    margin-left: 0.62rem;
                    font-size: 0.87rem;
                    font-weight: 700;
                    color: #5c5c5c;
                }
            }



            svg:nth-child(2) {
                margin-right: 1.87rem;
                transition: transform 0.2s ease-in-out;

            }

            svg:nth-child(3) {
                transition: transform 0.2s ease-in-out;
            }

            .priceBlock:hover {
                transform: scale(1.2);
                cursor: pointer;

                .cart {
                    path {
                        stroke: #a5d364;
                    }
                }
            }

            .favorite:hover {
                transform: scale(1.2);
                cursor: pointer;

                path {
                    fill: #ff8585;
                }
            }

            .profile:hover {
                transform: scale(1.2);
                cursor: pointer;

                path {
                    fill: #a5d364;
                }
            }

        }
    }
}